import React from 'react';
import styles from './seasonList.module.css';

const SeasonList = ({seasonName, seasonValue, seasonChange, season}) => {
    return (
        <>
            {   
                <li className={season == seasonValue ? styles.active : styles.seasonlist} value={seasonValue} onClick={(e) => seasonChange(e.target.value)} >{seasonName}</li>
            }
        </>
    );
};

export default SeasonList;