import React, { useRef, useState } from 'react';
import AreaCode from './areaCode';
import Detail from './detail';
import FestivalCreate from './festivalCreate';
import styles from './main.module.css';
import PageNumber from './pageNumber';
import Recommend from './recommend';
import SeasonList from './seasonList';

const Main = ({onClickDetail, ftv, year, mon, area, areaChange, minus, plus, seasonChange, season, show, showDown}) => {
    
    const areaCode = [
        {name: '전국', value: ''},
        {name: '서울', value: '1'},
        {name: '인천', value: '2'},
        {name: '대전', value: '3'},
        {name: '대구', value: '4'},
        {name: '광주', value: '5'},
        {name: '부산', value: '6'},
        {name: '울산', value: '7'},
        {name: '세종시', value: '8'},
        {name: '경기도', value: '31'},
        {name: '강원도', value: '32'},
        {name: '충청북도', value: '33'},
        {name: '충청남도', value: '34'},
        {name: '경상북도', value: '35'},
        {name: '경상남도', value: '36'},
        {name: '전라북도', value: '37'},
        {name: '전라남도', value: '38'},
        {name: '제주도', value: '39'},
        
    ];

    const seasonList = [
        {name: '#봄', value: '1'},
        {name: '#여름', value: '2'},
        {name: '#가을', value: '3'},
        {name: '#겨울', value: '4'},
        {name: '#4계절', value: '5'},
    ]

  




    return (
        <>
           
                    <ul className={styles.season}>
                        {
                            seasonList.map((list, index)=> <SeasonList key={index} seasonName={list.name} seasonValue={list.value} season={season} seasonChange={seasonChange} /> )
                        }
                    </ul>

                    <ul className={show == 1 ? styles.down : styles.recommend}>
                        {   
                            season == 1 ?
                            ftv.map((item, index)=> 
                                (item.eventstartdate > 20210228 &&  item.eventenddate < 20210601) &&
                                <Recommend  key={index} item={item} />)
                            : season == 2 ?
                            ftv.map((item, index)=> 
                                (item.eventstartdate > 20210531 &&  item.eventenddate < 20210901) &&
                                <Recommend  key={index} item={item} />)
                            : season == 3 ?
                            ftv.map((item, index)=> 
                                (item.eventstartdate > 20210831 &&  item.eventenddate < 20211201) &&
                                <Recommend  key={index} item={item} />)
                            : season == 4 ?
                            ftv.map((item, index)=> 
                                item.eventstartdate >= 20211201  &&
                                <Recommend  key={index} item={item} />)
                            :
                            ftv.map((item, index)=> 
                                (item.eventstartdate === 20210101  && item.eventenddate === 20211231) &&
                                <Recommend  key={index} item={item} />)
                        }
                    </ul>
                    <button className={styles.plusButton} onClick={()=> showDown()}><i class="fas fa-plus-circle"></i> 더보기</button>

            
            <div className={styles.selectbox}>
                <ul className={styles.area}  >
                    {
                        areaCode.map((list, index) => <AreaCode key={index} areaName={list.name} area={area} areaValue={list.value} areaChange={areaChange} />) 
                    }

                </ul>
            </div>

            
            <div className={styles.selectDate}>
                <button className={styles.arrowLeft} onClick={()=> minus(mon)}><i className="fas fa-caret-left"></i></button>
                <p className={styles.arrowCenter}>{mon + '월'}</p>
                <button className={styles.arrowRight} onClick={()=> plus(mon)}><i className="fas fa-caret-right"></i></button>
            </div>
            
            <ul className={styles.ulList}>
                {   ftv && (ftv.length > 1 
                        ? ftv.map((item, index)=>  
                                  (item.eventstartdate <= Number(year + (mon < 10 ? '0' : '') + mon + '01')  && item.eventenddate >= Number(year + mon + '01')) && 
                                <FestivalCreate key={index} item={item} onClickDetail={onClickDetail} /> 
                        )

                        :  <FestivalCreate item={ftv} onClickDetail={onClickDetail} /> )
                       
                    
                }



            </ul>
 
        </>
    );
};

export default Main;